.copy-buttons {
  display: block;
  margin-bottom: 10px; }
  .copy-buttons__header {
    text-align: center;
    margin: 15px 0 5px;
    user-select: none; }
  .copy-buttons__buttons-container {
    display: flex;
    flex-direction: row;
    justify-content: space-around; }
    .copy-buttons__buttons-container__hex-btn, .copy-buttons__buttons-container__hsl-btn {
      padding: 5px;
      border: 1px solid gray;
      border-radius: 15px;
      user-select: none;
      -webkit-tap-highlight-color: transparent; }
    .copy-buttons__buttons-container__hex-btn:hover, .copy-buttons__buttons-container__hsl-btn:hover {
      cursor: pointer;
      border-color: #03a9f4; }

@media screen and (max-width: 824px) and (max-height: 824px) {
  .copy-buttons {
    width: 100%; }
    .copy-buttons__header {
      position: absolute;
      top: 35px;
      left: calc(50% - 100px);
      width: 200px;
      margin: 0;
      color: gray; }
    .copy-buttons__buttons-container {
      position: absolute;
      top: 40px;
      flex-direction: column;
      justify-content: space-between;
      height: 80px;
      width: 50px;
      margin: 30px 30px 0;
      text-align: center; } }

@media screen and (max-width: 824px) and (max-height: 450px) {
  .copy-buttons {
    display: flex;
    flex-direction: row;
    width: 50vw; }
    .copy-buttons__header {
      position: absolute;
      left: 240px;
      top: 40px;
      width: fit-content;
      margin: 0 10px 0 0;
      text-align: left;
      line-height: 30px; }
    .copy-buttons__buttons-container {
      position: absolute;
      top: 40px;
      left: 120px;
      flex-direction: row;
      justify-content: flex-start;
      height: 30px;
      width: 100px;
      margin: 0;
      text-align: center; }
      .copy-buttons__buttons-container__hsl-btn {
        margin-left: 10px; } }
