.color-picker {
  display: block;
  margin: 15px auto;
  width: fit-content; }
  .color-picker__saturation {
    position: relative;
    height: 200px;
    width: 200px;
    touch-action: manipulation; }
  .color-picker__hue {
    position: relative;
    height: 15px;
    width: 200px;
    padding: 0;
    border-radius: 7.5px;
    margin: 10px 0 0;
    overflow: hidden;
    background-color: red;
    touch-action: manipulation; }
    .color-picker__hue .hue-horizontal {
      margin: 0 7.5px !important;
      -webkit-tap-highlight-color: transparent; }
  .color-picker__hue-pointer {
    position: absolute;
    z-index: 1;
    top: 10px;
    transform: translate(-50%, -10px);
    cursor: pointer;
    height: 15px;
    width: 15px;
    border-top: 1px solid gray;
    border-bottom: 1px solid gray;
    border-radius: 7.5px;
    box-shadow: inset 0 0 7px 1px rgba(0, 0, 0, 0.2); }

@media screen and (max-width: 824px) and (max-height: 824px) {
  .color-picker__saturation {
    position: relative;
    height: 50vh;
    width: auto;
    margin: 0 10px; }
  .color-picker__hue {
    position: relative;
    height: 30px;
    width: calc(100vw - 20px);
    border-radius: 15px;
    margin: 10px 10px 0; }
    .color-picker__hue .hue-horizontal {
      margin: 0 15px !important; }
  .color-picker__hue-pointer {
    height: 30px;
    width: 30px;
    border-radius: 15px; } }

@media screen and (max-width: 824px) and (max-height: 450px) {
  .color-picker {
    display: inline-block;
    height: calc(100vh - 130px);
    width: calc(100vw - 20px);
    margin: 0; }
    .color-picker__saturation {
      position: absolute;
      top: 120px;
      height: calc(100vh - 130px);
      width: calc(100vw - 20px); }
    .color-picker__hue {
      position: absolute;
      z-index: 3;
      top: 80px;
      left: 120px;
      width: calc(100% - 130px);
      margin: 0; } }
