.side-menu {
  position: relative;
  z-index: 3;
  height: 100vh;
  flex: 0 0 300px;
  margin: 0;
  background-color: white;
  box-shadow: 1px 0 5px 0 rgba(0, 0, 0, 0.75);
  transition: margin-left 0.3s cubic-bezier(0.645, 0.045, 0.355, 1); }
  .side-menu ::-webkit-scrollbar {
    width: 0; }
  .side-menu--hidden {
    margin-left: -280px; }
  .side-menu__container {
    display: inline-block !important;
    height: 100%;
    width: calc(100% - 20px);
    background-color: white;
    overflow-x: hidden;
    overflow-y: auto; }
  .side-menu__title {
    position: absolute;
    z-index: 2;
    right: 0;
    top: 0;
    height: 40px;
    width: 75%;
    margin: 0;
    padding: 0 !important;
    border-radius: 0 0 0 30px;
    background-color: #262626;
    opacity: 1;
    overflow: hidden;
    transition: opacity .3s ease; }
  .side-menu .gradient-effect {
    display: block;
    height: 100%;
    width: 100%;
    color: white;
    font-family: 'Pacifico', cursive;
    font-size: 18px;
    font-weight: normal;
    line-height: 40px;
    text-align: center;
    user-select: none;
    background: linear-gradient(280deg, #f40303, #ffa500, #fbfb11, #24ce24, #7bc9e3, #3b84cc, #8a2be2);
    background-size: 1400% 1400%;
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    animation: AnimationName 14s ease infinite; }

@keyframes AnimationName {
  0% {
    background-position: 0% 50%; }
  50% {
    background-position: 100% 51%; }
  100% {
    background-position: 0% 50%; } }
  .side-menu--hidden .side-menu__title {
    opacity: 0; }
  .side-menu__colors-container {
    display: grid;
    height: 100px;
    width: 100px;
    margin: 70px auto 0;
    grid-template-columns: repeat(2, 50px); }
  .side-menu__hide-btn {
    display: inline-block;
    height: 100%;
    width: 20px;
    position: relative;
    overflow: hidden;
    user-select: none;
    transition: right .4s .5s ease; }
  .side-menu__hide-btn::before {
    content: "";
    position: absolute;
    z-index: 3;
    top: calc(50% - 10px);
    width: 20px;
    height: 20px;
    background-image: url(images/cef293dc5ca4c3340d3a50af8f593ded.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 16px;
    transition: transform .4s ease; }
  .side-menu__hide-btn:hover {
    cursor: pointer; }
  .side-menu--hidden .side-menu__hide-btn::before {
    transform: rotate(-180deg); }

@media screen and (max-width: 766px), screen and (max-height: 766px) {
  .side-menu {
    height: 100%;
    width: 100%;
    position: absolute;
    left: 0;
    box-shadow: none;
    transition: transform 0.4s cubic-bezier(0.77, 0, 0.175, 1) 0s; }
    .side-menu--hidden {
      margin-left: 0;
      transform: translateX(-100%); }
    .side-menu__container {
      display: block;
      height: 100%;
      width: 100%; }
    .side-menu__title {
      height: 30px;
      width: 200px;
      border-radius: 0 0 0 25px;
      padding: 0 0 0 20px !important; }
    .side-menu .gradient-effect {
      text-align: left;
      line-height: 30px; }
    .side-menu__colors-container {
      margin-top: 60px; }
    .side-menu__hide-btn {
      position: absolute;
      z-index: 5;
      top: 3px;
      right: 3px;
      height: 24px;
      width: 24px;
      padding: 0;
      -webkit-tap-highlight-color: transparent; }
    .side-menu__hide-btn::before {
      top: 0;
      padding: 2px;
      border-radius: 11px;
      background-color: white;
      background-position-y: center;
      background-position-x: 35%; }
    .side-menu--hidden .side-menu__hide-btn {
      right: -28px; } }

@media screen and (max-width: 824px) and (max-height: 450px) {
  .side-menu__title {
    width: 47%; }
  .side-menu__colors-container {
    position: absolute;
    height: 100px;
    width: 100px;
    margin: 10px !important; } }
