* {
  box-sizing: border-box;
  font-family: 'Noto Sans KR', sans-serif;
  font-size: 14px;
  font-weight: normal; }

html,
body {
  margin: 0;
  height: 100vh;
  overflow: hidden; }

input {
  border: none;
  -webkit-tap-highlight-color: transparent;
  background: white no-repeat;
  background-image: linear-gradient(to bottom, #03a9f4, #03a9f4), linear-gradient(to bottom, silver, silver);
  background-size: 0% 2px, 60% 1px;
  background-position: 100% 100%, 100% 100%;
  transition: background-size 0.3s cubic-bezier(0.64, 0.09, 0.08, 1); }

input:focus {
  outline: none;
  border: none;
  background-size: 60% 2px, 60% 1px; }

a,
a:link,
a:visited,
a:hover,
a:active {
  color: inherit;
  text-decoration: none; }

::-webkit-scrollbar {
  width: 14px;
  height: 18px; }

::-webkit-scrollbar-button {
  width: 0;
  height: 0;
  display: none; }

::-webkit-scrollbar-corner {
  background-color: transparent; }

::-webkit-scrollbar-thumb {
  height: 6px;
  border: 4px solid rgba(0, 0, 0, 0);
  background-clip: padding-box;
  border-radius: 7px;
  background-color: rgba(0, 0, 0, 0.15);
  box-shadow: inset -1px -1px 0 rgba(0, 0, 0, 0.05), inset 1px 1px 0 rgba(0, 0, 0, 0.05); }
  ::-webkit-scrollbar-thumb:hover {
    background-color: rgba(0, 0, 0, 0.25); }

.app,
.container {
  display: flex;
  justify-content: start;
  flex-direction: row;
  min-height: 100%;
  min-width: 100%;
  position: relative; }

@media screen and (max-width: 824px) and (max-height: 824px) {
  input {
    background-size: 0% 2px, 100% 1px;
    background-position: 50% 100%, 100% 100%;
    transition: background-size 0.3s cubic-bezier(0.64, 0.09, 0.08, 1); }
  input:focus {
    background-size: 100% 2px, 100% 1px; }
  ::-webkit-scrollbar {
    width: 10px;
    height: 18px; } }
